
a {
  color: black;
  text-decoration: none;
}
input[type='text'],
input[type='number'],
textarea {
  font-size: 16px;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 4px;
}
::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .5);
  -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

html, body, #root {
  margin: 0;
  font-family: "Mountains of Christmas";
  height: 100% !important;
  text-align: center;
}

.App {
  height: 55%;
  width: 100%;
  top: 20%;
  position: fixed;
  margin: auto;
  justify-content: center;
  display: flex;
  align-items: center;
}

.App-header {
  background-color: #fff9c2;
  padding: 5px;
  min-height: 20vh;
  justify-content: center;
  color: black;
  font-family: "Mountains of Christmas";
  width: 100%;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.1);
  position: fixed;
}

input {
    border: 3px solid white;
    border-radius: 5px;
    padding: 15px;
    width: 80%;
    background: rgba(255,255,255,0.6);
    margin: 0 0 10px 0;
}

